.button {
  overflow: hidden;
  position: relative;
  border-radius: 35px;
  padding: 16px 70px 16px 35px;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  background-color: transparent;
  border: 1px solid #ffb400;
  font-family: 'Poppins', sans-serif;
  font-size: 15px;
  font-weight: 600;
  color: #fff;
  z-index: 9;

  &::before {
    z-index: -1;
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    transform: translateX(100%);
    transition: .3s ease-out all;
    background: #ffb400;
  }

  &:hover:before {
    transform: translateX(0);
  }

  &__icon {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    right: -1px;
    height: 100%;
    width: 55px;
    border-radius: 50%;
    background: #ffb400;
    z-index: 3;

    &__image {
      width: 25px;
      height: 25px;
    }
  }
}


